.insights-card {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: all 0.2s ease;
}
.insights-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  border-color: #d1d5db;
}
.insights-card .card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}
.insights-card .card-header .card-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
}
.insights-card .card-header .insights-count {
  padding: 0.25rem 0.75rem;
  background: #f3f4f6;
  color: #6b7280;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
}
.insights-card .insights-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.insights-card .insights-list .insight-item {
  border: 1px solid #e5e7eb;
  border-radius: 0.75rem;
  padding: 1rem;
  transition: all 0.2s ease;
}
.insights-card .insights-list .insight-item:hover {
  border-color: #d1d5db;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.1);
}
.insights-card .insights-list .insight-item.impact-high {
  border-left: 4px solid #dc2626;
  background: #fef2f2;
}
.insights-card .insights-list .insight-item.impact-medium {
  border-left: 4px solid #f59e0b;
  background: #fffbeb;
}
.insights-card .insights-list .insight-item.impact-low {
  border-left: 4px solid #22c55e;
  background: #f0fdf4;
}
.insights-card .insights-list .insight-item .insight-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 0.75rem;
}
.insights-card .insights-list .insight-item .insight-header .insight-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  background: #f3f4f6;
  border-radius: 0.5rem;
  color: #6b7280;
  flex-shrink: 0;
}
.insights-card .insights-list .insight-item .insight-header .insight-icon svg {
  width: 1.25rem;
  height: 1.25rem;
}
.insights-card .insights-list .insight-item .insight-header .insight-title {
  flex: 1;
  font-size: 1rem;
  font-weight: 600;
  color: #1f2937;
}
.insights-card .insights-list .insight-item .insight-header .impact-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}
.insights-card .insights-list .insight-item .insight-header .impact-badge.impact-high {
  background: #fecaca;
  color: #dc2626;
}
.insights-card .insights-list .insight-item .insight-header .impact-badge.impact-medium {
  background: #fde68a;
  color: #d97706;
}
.insights-card .insights-list .insight-item .insight-header .impact-badge.impact-low {
  background: #bbf7d0;
  color: #166534;
}
.insights-card .insights-list .insight-item .insight-description {
  font-size: 0.875rem;
  color: #6b7280;
  line-height: 1.5;
  margin-bottom: 0.75rem;
}
.insights-card .insights-list .insight-item .insight-details {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 0.75rem;
}
.insights-card .insights-list .insight-item .insight-details .confidence-chip {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.25rem 0.5rem;
  background: #e0e7ff;
  color: #4338ca;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 500;
}
.insights-card .insights-list .insight-item .insight-details .confidence-chip svg {
  width: 0.75rem;
  height: 0.75rem;
}
.insights-card .insights-list .insight-item .insight-details .category-badge {
  padding: 0.25rem 0.5rem;
  background: #f3f4f6;
  color: #6b7280;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: capitalize;
}
.insights-card .insights-list .insight-item .insight-action {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  padding: 0.75rem;
  background: #f8fafc;
  border-radius: 0.5rem;
  border: 1px solid #e2e8f0;
}
.insights-card .insights-list .insight-item .insight-action .action-text {
  flex: 1;
  font-size: 0.875rem;
  color: #374151;
  line-height: 1.5;
}
.insights-card .insights-list .insight-item .insight-action .action-btn {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.5rem 0.75rem;
  background: #3b82f6;
  color: white;
  border: none;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}
.insights-card .insights-list .insight-item .insight-action .action-btn:hover {
  background: #2563eb;
  transform: translateY(-1px);
}
.insights-card .insights-list .insight-item .insight-action .action-btn svg {
  width: 0.875rem;
  height: 0.875rem;
}
.insights-card .empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem 1rem;
  text-align: center;
}
.insights-card .empty-state .empty-icon {
  color: #d1d5db;
  margin-bottom: 1rem;
}
.insights-card .empty-state .empty-title {
  font-size: 1.125rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.5rem 0;
}
.insights-card .empty-state .empty-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.5;
}

@media (max-width: 768px) {
  .insights-card {
    padding: 1rem;
  }
  .insights-card .card-header {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
    text-align: center;
  }
  .insights-card .insights-list .insight-item .insight-header {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
    text-align: center;
  }
  .insights-card .insights-list .insight-item .insight-details {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
  }
  .insights-card .insights-list .insight-item .insight-action {
    flex-direction: column;
    align-items: stretch;
    gap: 0.75rem;
  }
  .insights-card .insights-list .insight-item .insight-action .action-btn {
    justify-content: center;
  }
}
